function showErrMsg(elContainer, msgContainer, msg) {
    if (!elContainer || !msgContainer) {
        console.error('Invalid Arguments - showErrMsg()');
        return;
    };
    msgContainer.innerHTML = msg;
    // elContainer.classList.remove('h-0')
    elContainer.classList.remove('opacity-0')
    elContainer.classList.add('opacity-100')
    setTimeout(hideErrMsg, 1000, elContainer);
}
function hideErrMsg(el) {
    if (!el) {
        console.error('hideErrMsg:Invalid Arguments');
        return;
    };
    el.classList.add('opacity-0');
    el.classList.remove('opacity-100');
    // el.classList.add('h-0');
}

function disableButton(el) {
    el.classList.add('disable-btn');
}
function enableButton(el) {
    el.classList.remove('disable-btn');
}

function percentageOfBNBRaised(amount = 1700, HARDCAP = 16500) {
    // const raisedAmount = 1.7;
    const raisedAmount = amount; //testing
    const percentageExtended = (raisedAmount / HARDCAP) * 100;
    const percentage = Math.round(percentageExtended * 100) / 100
    return +percentage;
}

function updateProgressBar(percentage) {
    const progressBars = document.querySelectorAll('.sale-progress');
    progressBars.forEach(el => {
        el.value = percentageOfBNBRaised();
    });
}

/**
 * The below code works with following HTML

    <div class="relative">
        <input type="text" placeholder="Amount (IBAT)" class="buy-amount" id="ibat-amount">
        <span id="ibat-amount-span">IBAT</span>
    </div>
    <div class="relative">
        <input type="text" placeholder="Total (BNB)" class="buy-amount" id="bnb-amount">
        <span id="bnb-amount-span">BNB</span>
    </div>

 */
const IBAT_PER_BNB = +156250;
const ibatAmount = document.querySelector('#ibat-amount');
const BNBAmount = document.querySelector('#bnb-amount');
BNBAmount.onkeyup = populateIBATAmount;
ibatAmount.onkeyup = populateBNBAmount;

function populateIBATAmount() {
    ibatAmount.value = calculateIBATAmount(BNBAmount.value)
}
function calculateIBATAmount(BNBAmount) {
    return +(BNBAmount * IBAT_PER_BNB_PRESALE);
}


function populateBNBAmount() {
    BNBAmount.value = calculateBNBAmount(ibatAmount.value)
}
function calculateBNBAmount(IBATAmount) {
    return +(IBATAmount / IBAT_PER_BNB_PRESALE);
}


function checkDataLayer() {
    window.dataLayer = window.dataLayer || [];
    errorCode = "";
    arguments = [{
        event: 'workflowStep',
        workflowName: 'connectWallet',
        workflowStepNumber: 1,
        workflowStepName: 'start',
        workflowCompleteFlag: 0,
        workflowErrorCode: { errorCode }
    }];
    console.log(arguments);
    window.dataLayer.push(arguments);
}
function hitDataLayer() {
    try {
        let walletAddress = getUserWalletAddress();
        window.dataLayer = window.dataLayer || [];
        arguments = [{
            event: 'workflowStep',
            workflowName: 'connectWallet',
            workflowStepNumber: 2,
            workflowStepName: 'successful',
            workflowCompleteFlag: 1,
            walletAddress: { walletAddress },
            workflowErrorCode: { errorCode }
        }];
        console.log("ln 190", arguments);
        console.log(window.dataLayer.push(arguments));
    } catch (e) { console.log(e.message); }
}